import { getRecentSongs } from '@/request/api/home';
import { mapMutations } from 'vuex';
export default {
  data() {
    return {
      // 最近播放歌曲(默认100首)
      list: [],
      // 请求得到的总数
      total: 0
    };
  },

  async mounted() {
    if (sessionStorage.getItem("cookie")) {
      const res = await getRecentSongs(sessionStorage.getItem("cookie")); // console.log('获取最近播放歌曲', res);

      this.list = res.data.data.list; // console.log('最近播放歌曲', this.list);

      this.total = res.data.data.total;
    }
  },

  methods: {
    // 格式化单曲序号
    formatIndex(index) {
      return this.$formatIndex(index);
    },

    // 时间戳转专辑发布日期
    formatPublishTime(time) {
      // 把返回的时间戳转为时间格式
      var date = new Date(time);
      var M = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1;
      var D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      var H = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
      var Min = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
      return M + '-' + D + ' ' + H + ':' + Min;
    },

    // 双击歌曲播放 (！歌单列表格式不一致)
    // playSong(index) {
    //   // 显示底部音乐播放组件
    //   this.updateIsPlayBox(1)
    //   // 更新播放列表
    //   this.updatePlayList(this.list)
    //   // 更新歌单名(歌曲来源)
    //   this.updateSongListName('最近播放')
    //   // 更新歌单 id(歌曲来源)
    //   this.updateSongListId(0)
    //   // 更新播放列表下标
    //   this.updatePlayListIndex(index)
    // },
    ...mapMutations(['updateIsPlayBox', 'updatePlayList', 'updateSongListName', 'updateSongListId', 'updatePlayListIndex'])
  }
};